@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400&display=swap);
header {
    text-align: center;
}

.logo {
    margin: 1rem;
    width: 8rem;
}

nav {
    display: flex;
    justify-content: space-evenly;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: var(--high-color);
    box-shadow: 0 -5px 10px 0 rgba(0, 104, 14, 0.2);
}

a {
    color: var(--line-color);
    text-decoration: none;
}

.menu-item {
    width: 20vw;
    height: auto;
    border: none;
}

.menu-icon {
    font-size: 8rem;
    line-height: 5rem;
    font-weight: 300;
    border-radius: 100%;
}

.menu-icon span {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background: #c1c1c1;
    border-radius: 100%;
}

.menu-icon span.menu-icon-doit {
    background: var(--line-color);
}

.menu-text {
    font-size: 1.5rem;
    line-height: 5rem;
    text-transform: uppercase;
}


.here {
    color: #1dff75;
}

.menu-item.here .menu-icon span {
    background: #1dff75;
    border-color: #1dff75;
}

.main-cell {
    width: 5rem;
}

.type-cell {
    width: 100%;
    height: 100%;
    font-size: 2rem;
}

.type-cell.grey {
    color: #797979;
}

.week-spots {
    background: rgba(255,255,255,0.5);
}

.spot.grey {
    opacity: 0.4;
}

.spot.open {
    border: var(--main-border);
    background: var(--high-color);
}

.spot.closed {
    border: var(--main-border);
    background: var(--line-color);
}

.allDone .spot.closed {
    border: #1dff75;
    background: #1dff75;
}

.number-modal {
    position: fixed;
    top: 25vh;
    left: 0;
    right: 0;
    width: 90%;
    padding: 30px;
    color: var(--line-color);
    background: var(--high-color);
    box-shadow: var(--main-shadow);
}

.number-modal h3 {
    font-size: large;
}

.number-modal label {
    font-size: large;
}

.number-modal fieldset {
    border: 0;
}

.number-modal fieldset div {
    text-align: left;
}

.number-modal input[type='radio'] {
    margin: 10px;
}

.number-modal input[type='number'] {
    -webkit-appearance: none;
            appearance: none;
    -moz-appearance: textfield;
    display: block;
    height: 30px;
    font-size: large;
    border: var(--main-border);
}

.number-modal-bg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
}

.save-todo {
    width: 40%;
    margin: 20px;
    color: #fff;
    background: #868fd9;
    border-radius: 5px;
}

table {
    width: 100%;
    margin: 0 auto 2rem auto;
    border-collapse: collapse;
}

tr {
    height: 5rem;
}

td {
    font-size: 3rem;
    border: var(--base-border);
    box-shadow: var(--main-shadow);
}

.day {
    width: 5rem;
    background: var(--high-color);
}

.left-column {
    text-align: left;
    padding: 0 0 0 10px;
}

.week-item-delete {
    float: left;
    width: 3rem;
    height: 3rem;
    margin: auto 5px auto auto;
    font-size: 2rem;
    line-height: 2rem;
}

.week-number-arrow-up {
    background: rgba(255, 0, 0, 0.4) url(/turtledot-x/static/media/arrowUp.d1f00b13.svg) center/cover;
}

.week-number-arrow-down {
    background: rgba(255, 0, 0, 0.4) url(/turtledot-x/static/media/arrowDown.0010f90a.svg) center/cover;
}

.allDone, .allDone .week-spots {
    color: var(--high-color);
    background: var(--line-color);
}

.scooch {
    box-shadow: -2px -2px 2px 0 var(--line-color);
    transform: translate(2px, 2px);
    transition: all ease-in-out;
}
.items-list {
    overflow-y: scroll;
    width: 100%;
    max-height: 25vh;
    font-size: 2rem;
    border: var(--base-border);
    background: var(--high-color);
    box-shadow: var(--main-shadow);
}

.items-list-item {
    width: 100%;
    height: 3rem;
    padding: 1vh 0 2.5vh 0;
    font-size: 2rem;
    border: 0;
    border-bottom: var(--base-border);
    border-radius: 0;
}

.items-list-item:last-child {
    border-bottom: 0;
}

.addAllItems {
    width: 100%;
    color: var(--base-color);
    background: var(--line-color);
    border-radius: 0;
}

.addAllItems.copy {
    background: #240050;
}

.week {
    margin: 0 auto 5vh auto;
}

.week-date {
    border: none;
}

.week-date div {
    height: 4rem;
    font-family: var(--main-font);
    font-weight: 400;
    font-size: 2.5rem;
    line-height: 4rem;
    padding: 0;
    background: var(--base-color);
    border: none;
}

html {
    --color0: #FFD951;
    --color1: #32DAFF;
    --color2: #3BF384;
    --color3: #F19FFF;
    --color4: #AE92FF;
    --color5: #FFFFFF;
    --color6: #9C9C9C;
    --color7: #FF9900;
    --color8: #5e6eff;
    --color9: #D46A83;
}

.color0 {
    background: #FFD951;
    background: var(--color0);
}

.color1 {
    background: #32DAFF;
    background: var(--color1);
}

.color2 {
    background: #3BF384;
    background: var(--color2);
}

.color3 {
    background: #F19FFF;
    background: var(--color3);
}

.color4 {
    background: #AE92FF;
    background: var(--color4);
}

.color5 {
    background: #FFFFFF;
    background: var(--color5);
}

.color6 {
    background: #9C9C9C;
    background: var(--color6);
}

.color7 {
    background: #FF9900;
    background: var(--color7);
}

.color8 {
    background: #5e6eff;
    background: var(--color8);
}

.color9 {
    background: #D46A83;
    background: var(--color9);
}

.item {
    display: flex;
    justify-content: space-between;
    margin: 0 0 2rem 0;
}

.item input {
    height: 5rem;
    font-family: var(--main-font);
    font-weight: 400;
    font-size: 3rem;
    line-height: 5rem;
    padding: 0;
    background: var(--high-color);
    border: var(--base-border);
}

.item .text {
    width: calc(33rem - 35px);
    height: 5rem;
    font-size: 3rem;
    line-height: 5rem;
    text-align: left;
    padding: 0 0 0 5px;
    background: var(--high-color);
    border: var(--base-border);
    box-shadow: var(--main-shadow);
}

.item .type {
    line-height: 32px;
}

.item .number {
    width: 8rem;
    border-radius: 5rem;
    -moz-appearance: textfield;
}

.edit-box {
    width: 100%;
    margin: 0 auto 2rem auto;
}

.edit-box-buttons {
    height: 10rem;
}

button.edit-complete {
    float: right;
    margin: 1rem;
    line-height: 36px;
}

.palette {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    background: var(--high-color);
    padding: 0.5rem;
}

.palette-square {
    width: 10.5rem;
    height: 10.5rem;
    margin: 0.5rem auto;
    border-radius: 0;
}

.info {
    position: absolute;
    top: 10px;
    right: 10px;
}

.info-box {
    position: fixed;
    width: calc(100vw - 20px);
    height: calc(100vh - 110px);
    top: 40px;
    left: 10px;
    padding: 0 20px 20px 20px;
    background: rgba(255, 255, 255, 1);
    overflow-y: scroll;
}

.info-box ul {
    margin: 0 0 40px 10px;
    padding: 0;
}

.info-box li, .info-box i {
    margin: 10px 0;
    padding: 0;
    font-size: 14px;
    text-align: left;
    line-height: 18px;
}

.info-box li.indent {
    list-style-position: inside;
    list-style-type: circle;
    font-weight: 300;
}

.bgPanel {
  z-index: -1;
  position: fixed;
  width: 80vw;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  opacity: 0.3;
  box-shadow: var(--main-shadow);
}

h1 {
  margin: 2rem;
  font-size: 4rem;
  font-weight: 400;
  text-transform: uppercase;
}

h2 {
  margin: 3rem;
  font-size: 3rem;
  font-weight: 300;
  text-transform: uppercase;
}

main {
  margin: 0 auto 20vh auto;
}

button {
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 3rem;
  padding: 0;
  background: var(--high-color);
  border: var(--base-border);
  border-radius: 100%;
  box-shadow: var(--main-shadow);
}

button.plus-sign {
  font-size: 5rem;
  font-weight: 300;
  line-height: 0;
}

button.add {
  margin: 2vh auto 4vh auto;
}

button.delete, .delete {
  transform: rotate(-45deg);
}

.done {
  background: var(--line-color);
}

.spot {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
}

@media screen and (min-height: 800px) {

  main {
    max-width: 800px;
  }

}

* {
  --base-color: #efefef;
  --high-color: #ffffff;
  --line-color: #000000;
  --main-font: 'Josefin Sans', sans-serif;
  --main-border: 2px solid #000000;
  --base-border: 1px solid #efefef;
  --main-shadow: 0 20px 20px 0 rgba(0, 104, 14, 0.2);
  font-size: 1.6vw;
  font-family: var(--main-font);
  text-align: center;
  box-sizing: border-box;
  margin: auto;
}

body {
  min-height: 100vh;
  background: linear-gradient(to bottom right, #bae1ff, #daffe9);
}

@media screen and (min-height: 800px) {
  * {
    font-size: 10px;
  }
}

